<template>
  <section id="alarm-level-page" class="df">
    <div class="left-tree tree">
      <tree :nodes="nodes" :setting="treeSetting" ref="tree" />
    </div>
    <el-tabs class="tabs df" v-model="curView">
      <el-tab-pane label="告警等级" name="1">
        <div class="tab-content df dfv">
          <div class="query-form df">
            <el-form :inline="true" :label-width="'100px'">
              <el-form-item :label="'规则名称'" prop="name">
                <el-input size="mini" v-model="alarmLevelQuery.name"></el-input>
              </el-form-item>

              <el-form-item :label="'企业名称'" prop="enterpriseName">
                <el-input size="mini" v-model="alarmLevelQuery.enterpriseName"></el-input>
              </el-form-item>

              <el-form-item :label="'服务名称'" prop="serverName">
                <el-input size="mini" v-model="alarmLevelQuery.serverName"></el-input>
              </el-form-item>
              
              <el-form-item :label="'操作用户'" prop="operatorName">
                <el-input size="mini" v-model="alarmLevelQuery.operatorName"></el-input>
              </el-form-item>

              <el-button type="primary" size="mini" @click="getAlarmLevelList()" style="margin-left: 33px;">确认</el-button>
              <el-button type="primary" size="mini" @click="alarmLevelQuery = {}">清除</el-button>
            </el-form>
          </div>          

          <div class="list df dfv">
            <div class="list-header">
              <h3>告警等级列表</h3>
              <div class="btn-group">
                <el-button type='primary' size="mini" @click="addAlarmLevelToModal()">添加</el-button>
              </div>
            </div>
            <div class="table-box df dfv">
              <el-table height="100%" :data="alarmLevelList" :tree-props="{children: 'children'}"  row-key="id" :row-class-name="({row}) => row.invisible ? 'invisible' : ''">
                <el-table-column label="告警等级" prop="level"></el-table-column>
                <el-table-column label="规则名称" prop="name">
                </el-table-column>
                <el-table-column label="企业名称">
                  <template slot-scope="scope">
                    <div v-if="scope.row.isColor" class="alarm-color" :style="'background: ' + scope.row.color"></div>
                    <span v-else>{{ scope.row.enterpriseName }}</span>
                  </template>
                </el-table-column>
                <el-table-column label="服务名称" prop="serverName"></el-table-column>
                <el-table-column label="操作用户" prop="operator.name" :formatter="$tableFilter('nullFilter')"></el-table-column>
                <el-table-column label="状态" prop="state"></el-table-column>
                <el-table-column label="创建时间" prop="updateTime">
                  <template slot-scope="scope">
                    <span v-if="scope.row.showOps">{{ scope.row.updateTime | dateFormatFilter}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <a v-show="scope.row.showOps && scope.row.defaultLevel === '否'" @click="addAlarmLevelToModal(scope.row)">修改</a>
                    <a v-show="scope.row.showOps && scope.row.defaultLevel === '否'" @click="deleteAlarmLevelToModal(scope.row)">删除</a>
                    <a v-show="scope.row.showOps && scope.row.defaultLevel === '否'" @click="changeAlarmLevelState(scope.row)">{{scope.row.state === '启用' ? '禁用' : '启用' }}</a>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <!-- Pagination -->
            <el-col :span="24" class="toolbar">
              <el-pagination
                layout="total, prev, pager, next,sizes, jumper"
                @current-change="alarmLevelPagi.handleCurrentChange"
                @size-change="alarmLevelPagi.pageSizeChange"
                :page-sizes="[10, 15, 50, 100]"
                :page-size="alarmLevelPagi.pageSize"
                :total="alarmLevelPagi.total"
                style="float:right;">
              </el-pagination>
            </el-col>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="设备等级" name="2">
        <div class="tab-content df dfv">
          <div class="query-form df">
            <el-form :inline="true" :label-width="'100px'">
              <!-- <el-form-item :label="'设备名称'" prop="upSize">
                <el-input size="mini" v-model="deviceLevelQuery.upSize"></el-input>
              </el-form-item> -->
              <el-form-item :label="'设备类型'" prop="deviceType">
                <el-input size="mini" v-model="deviceLevelQuery.deviceType"></el-input>
              </el-form-item>
              <el-form-item :label="'设备型号'" prop="deviceModel">
                <el-input size="mini" v-model="deviceLevelQuery.deviceModel"></el-input>
              </el-form-item>
              <el-form-item :label="'操作用户'" prop="operatorName">
                <el-input size="mini" v-model="deviceLevelQuery.operatorName"></el-input>
              </el-form-item>
              <el-button type="primary" size="mini" @click="getDeviceLevelList()" style="margin-left: 33px;">确认</el-button>
              <el-button type="primary" size="mini" @click="deviceLevelQuery = {}">清除</el-button>
            </el-form>
          </div>
          <div class="list df dfv">
            <div class="list-header">
              <h3>设备等级列表</h3>
              <div class="btn-group">
                <el-button type='primary' size="mini" @click="addDeviceLevelToModal()">添加</el-button>
                <el-button type='primary' size="mini" @click="deleteDeviceToModal()">批量删除</el-button>
              </div>
            </div>
            <div class="table-box df dfv">
              <el-table height="100%" :data="deviceLevelList" ref="deviceLevelList">
                <el-table-column type="selection"></el-table-column>
                <el-table-column label="企业名称" prop="enterpriseName"></el-table-column>
                <el-table-column label="服务名称" prop="serverName"></el-table-column>
                <!-- <el-table-column label="设备名称" prop="level2"></el-table-column> -->
                <el-table-column label="设备类型" prop="deviceType"></el-table-column>
                <el-table-column label="设备型号" prop="deviceModel"></el-table-column>
                <el-table-column label="设备告警级别" width="110" prop="level5"></el-table-column>
                <el-table-column label="1" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[0] && 'selected'"></i></template></el-table-column>
                <el-table-column label="2" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[1] && 'selected'"></i></template></el-table-column>
                <el-table-column label="3" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[2] && 'selected'"></i></template></el-table-column>
                <el-table-column label="4" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[3] && 'selected'"></i></template></el-table-column>
                <el-table-column label="5" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[4] && 'selected'"></i></template></el-table-column>
                <el-table-column label="6" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[5] && 'selected'"></i></template></el-table-column>
                <el-table-column label="7" width="29"><template slot-scope="scope"><i class="level-select" :class="(scope.row.alarm || [])[6] && 'selected'"></i></template></el-table-column>
