            <el-option v-for="(item, index) in enterPriseList" :key="index" :label="item.title" :value="item.enterpriseCode">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务名称" prop="service">
          <el-select size="mini" v-model="addDeviceLevelForm.serverCode" @input="getDeviceList4AddDeviceLevel" style="width:240px">
            <el-option v-for="(item, index) in getServerList(addDeviceLevelForm.enterpriseCode)" :key="index" :label="item.title" :value="item.code">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备类型" prop="deviceType">
          <el-select size="mini" v-model="addDeviceLevelForm.deviceType" @input="addDeviceLevelForm.deviceModel = null" style="width:240px">
            <el-option v-for="(item, index) in addDeviceLevelForm.deviceTypeList" :key="index" :label="item.type" :value="item.type">{{ item.type }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备型号" prop="deviceModel">
          <el-select size="mini" v-model="addDeviceLevelForm.deviceModel" style="width:240px">
            <el-option v-for="(item, index) in ((addDeviceLevelForm.deviceTypeList || []).find(v => v.type === addDeviceLevelForm.deviceType) || {models: []}).models" :key="index" :label="item" :value="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="告警级别设置" prop="levels">
          <el-select size="mini" v-model="addDeviceLevelForm.levels" class="multiple" multiple style="width:240px">
            <el-option v-for="(item, index) in [0,1,2,3,4,5,6,7]" :key="index" :label="oneToEight[item] + '级告警'" :value="item + 1">{{ oneToEight[item] + '级告警' }}</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="addDeviceLevelVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="addDeviceLevelSubmit()">确 定</el-button>
      </span>
    </el-dialog>
    
    <el-dialog
      :title="addAlarmLevelForm.__type === 'add' ? '添加' : '修改'"
      class="m400"
      :close-on-click-modal = "false"
      @close="addAlarmLevelVisible = false"
      :visible="addAlarmLevelVisible">
      <el-form :model="addAlarmLevelForm" :rules="addAlarmLevelRules" label-width="90px" label-position="left" ref="addAlarmLevel" class="add-alarm-level-form">
        <el-form-item label="　规则名称" prop="name">
          <el-input v-model="addAlarmLevelForm.name" size="mini" style="width:190px"></el-input>
        </el-form-item>
        <el-form-item label="　操作用户" prop="machineType">
          <span>{{ curUser.name }}</span>
        </el-form-item>
        <el-form-item label="　企业名称" prop="enterpriseCode">
          <el-select size="mini" v-model="addAlarmLevelForm.enterpriseCode" style="width:190px">
            <el-option v-for="(item, index) in enterPriseList" :key="index" :label="item.title" :value="item.enterpriseCode">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="　服务名称" prop="serverCode">
          <el-select size="mini" v-model="addAlarmLevelForm.serverCode" style="width:190px">
            <el-option v-for="(item, index) in getServerList(addAlarmLevelForm.enterpriseCode)" :key="index" :label="item.title" :value="item.code">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="　告警等级">
          <el-select size="mini" v-model="addAlarmLevelForm.alarmLength" style="width:190px" @input="resetAllAlarmSettings">
            <el-option v-for="(item, index) in oneToEight" :key="index" :label="item + '级告警'" :value="index">{{ item + '级告警' }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="　等级名称及颜色设定" label-width="180px"></el-form-item>
        
        <el-form-item v-for="(item, index) in oneToEight.slice(0, addAlarmLevelForm.alarmLength + 1 || 0)" :key="index" :label="'　' + item + '警等级'" :prop="'addAlarmLevelForm' + index">
          <el-input v-model="addAlarmLevelForm.levelNames[index]" size="mini" style="width:190px"></el-input>
          <el-color-picker size="mini" v-model="addAlarmLevelForm.colors[index]" style="vertical-align: -10px; margin-left: 10px;"></el-color-picker>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="addAlarmLevelVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="addAlarmLevelSubmit();">确 定</el-button>
      </span>
    </el-dialog>
    
    <el-dialog
      title="设备告警级对应修改"
      class="m400"
      :close-on-click-modal = "false"
      @close="setRelationVisible = false"
      :visible="setRelationVisible">
      <el-form :model="setRelationForm" :rules="setRelationRules" label-width="100px" ref="setRelation" label-position="left">
        <el-form-item label="企业名称" prop="machineType">
          <span>{{ setRelationForm.enterpriseName }}</span>
        </el-form-item>
        <el-form-item label="服务名称" prop="machineType">
          <span>{{ setRelationForm.serverName }}</span>
        </el-form-item>
        <el-form-item label="设备类型" prop="machineType">
          <span>{{ setRelationForm.deviceType }}</span>
        </el-form-item>
        <el-form-item label="设备型号" prop="aaa">
          <span>{{ setRelationForm.deviceModel }}</span>
        </el-form-item>

        <el-form-item :label="setRelationForm.sourceLevelList[index] + '级告警'"  :prop="'relation' + index" v-for='(item ,index) in setRelationForm.sourceLevelList' :key="index">
          <el-select size="mini" v-model="setRelationForm.targetLevelList[index]" style="width:190px">
            <el-option v-for="(item, index) in (setRelationForm.targetLevelOptions || [])" :key="index" :label="item.levelName" :value="item.level">{{ item.levelName }}</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="setRelationVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="setRelationSubmit();">确 定</el-button>
      </span>
    </el-dialog>
  </section>  
</template>

<script>
export default {
  name: 'alarmLevel',
  data () {
    let scope = this;
    return { 
      curUser: {strId: '123123', name: 'chenht'},
      oneToEight: [ '一', '二', '三', '四', '五', '六', '七', '八' ],
      defaultAlarmColor: [ '#DB001B', '#FF8400', '#E6D500', '#C001E7', '#FF5695', '#00B1FF', '#78E2FA', '#7ED321' ],
      enterPriseList: [],
      nodes: [{
        name: '全部',
        children: [
          {
            name: 'diyige'
          }
        ]
      }],      
      treeSetting: {
        check: {
          enable: true,
          chkStyle: "checkbox",
          chkboxType: {
            Y: "ps",
            N: "ps"
          }
        },
        view: {
          showIcon: false,
          showLine: false,
          showTitle: false,
          dblClickExpand: false,
          fontCss: function(treeId, node) {
            return node.fsuState === false
              ? { color: "#9A9A9A" }
              : { color: "#fff" };
          }
        },
        data: {
          key: {
            children: "children",
            name: "name",
            title: "code"
          }
        },
        callback: {
          onClick: (event, treeId, treeNode) => {
            if (treeNode.nocheck === false) {
              //如果这个节点是可选的，单击文字的时候将选中此项
              let treeObj = scope.$refs.tree.ztreeObj
              treeObj.checkNode(treeNode, !treeNode.checked, true, true);
            }
          },
          beforeCheck: (treeId, treeNode) => {
            if (this.curDistrict === 0) { return true; }
            let invalid = true;
            const traversal = node => {
              let parent = node.getParentNode()
              if (node.level >= this.curDistrict) {
                if (parent.children.filter(v => v.checked && (v.check_Child_State === 2 || v.check_Child_State === -1)).length != parent.children.length - 1) { invalid = false; return; }
                traversal(parent)
              }
              return true
            }
            if (
              !treeNode.checked &&
              treeNode.level >= this.curDistrict && 
              traversal(treeNode) && invalid
            ) {
              return false;
            }
            return true;
          }
          // beforeExpand: beforeExpand
        }
      },
      curView: '1',
      alarmLevelQuery: {},
      deviceLevelQuery: {},
      relationQuery: {},
      alarmLevelList: [],
      deviceLevelList: [],
      relationList: [],
      alarmLevelPagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.alarmLevelPagi.page = v;
          this.getAlarmLevelList();
        },
        pageSizeChange: () => {
          this.alarmLevelPagi.pageSize = v;
          this.getAlarmLevelList();
        },
      },
      deviceLevelPagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.deviceLevelPagi.page = v;
          this.getDeviceLevelList();
        },
        pageSizeChange: () => {
          this.deviceLevelPagi.pageSize = v;
          this.getDeviceLevelList();
        },
      },
      relationPagi: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.relationPagi.page = v;
          this.getRelationList();
        },
        pageSizeChange: () => {
          this.relationPagi.pageSize = v;
          this.getRelationList();
        },
      },
      addDeviceLevelForm: {},
      addDeviceLevelVisible: false,
      addAlarmLevelForm: {},
      addAlarmLevelVisible: false,
      setRelationForm: {},
      setRelationVisible: false,
    }
  },
  computed: {    
    addDeviceLevelRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    addAlarmLevelRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
    setRelationRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
  },
  methods: {
    getUniqueServiceList () {
      this.$api.enterpriseLevelControllerGetUniqueServiceList().then(res => {
        this.enterPriseList = res;
      })
    },
    getServerList (enterpriseCode) {
      return (this.enterPriseList.find(v => v.enterpriseCode === enterpriseCode) || {serverCodes: []}).serverCodes
    },
    getAlarmLevelList () {
      this.$api.enterpriseLevelControllerList(Object.assign({
        pageSize: this.alarmLevelPagi.pageSize,
        curPage: this.alarmLevelPagi.page
      }, this.alarmLevelQuery)).then(res => {
        res.list.forEach((v, i) => {
          v.showOps = true;
          v.level = v.levels.length + '个等级'
          v.children = [{
            id: i + '-title',
            level: '等级名称',
            name: '自定义名称',
              operator: {name: ' '},
            enterpriseName: '告警颜色'
          }]
          v.levels.forEach((u, j) => {
            v.children.push({
              id: i + '-' + j,
              level: this.oneToEight[u - 1] + '级告警',
              name: v.levelNames[j],
              color: v.colors[j],
              operator: {name: ' '},
              isColor: true,
            })
          })
          
          if (v.children.length % 2 === 0) {
            v.children.push({
              id: i + '-invisible',
              invisible: true
            })
          }
        })
        this.alarmLevelList = res.list;
        this.alarmLevelPagi.total = res.count;
      })
    },
    getDeviceLevelList () {
      this.$api.deviceTypeLevelControllerList(Object.assign({
        pageSize: this.deviceLevelPagi.pageSize,
        curPage: this.deviceLevelPagi.page
      }, this.deviceLevelQuery)).then(res => {
        res.list.forEach(v => {
          v.alarm = [];
          v.levels.forEach(l => {
            v.alarm[l - 1] = true;
          })
        })
        this.deviceLevelList = res.list;
        this.deviceLevelPagi.total = res.count;
      })

    },
    getRelationList () {
      this.$api.alarmLevelControllerList(Object.assign({
        currentPage: this.relationPagi.page,
        pageSize: this.relationPagi.pageSize
      }, this.relationQuery)).then(res => {
        res.list.forEach((v, i) => {
          v.showOps = true;
          v.level = v.sourceLevelList.length + '个等级'
          v.children = [{
            id: i + '-title',
            enterpriseName: '源告警等级',
            serverName: '目标告警等级',
            deviceType: '自定义名称',
            deviceModel: '告警颜色',
            operator: {name: ' '}
          }]
          v.sourceLevelList.forEach((u, j) => {
            v.children.push({
              id: i + '-' + j,
              enterpriseName: u + '级告警',
              serverName: v.targetLevelList[j] + '级告警',
              color: v.colorList[j],
              deviceType: v.targetLevelNameList[j],
              operator: {name: ' '},
              isColor: true,
            })
          })
          
          if (v.children.length % 2 === 0) {
            v.children.push({
              id: i + '-invisible',
              invisible: true
            })
          }
        })
        this.relationList = res.list;
        this.relationPagi.total = res.count;
      })
    },
    getDeviceList4AddDeviceLevel () {
      this.$api.deviceTypeLevelControllerGetDeviceTypeList({
        enterpriseCode: this.addDeviceLevelForm.enterpriseCode,
        serverCode: this.addDeviceLevelForm.serverCode,
      }).then(res => {
        let obj = {}
        let list = []
        res.deviceTypeResultList.forEach(v => {
          if (!obj[v.deviceType]) { obj[v.deviceType] = {type: v.deviceType, models: []} }
          obj[v.deviceType].models.push(v.model);
        })
        for (let i in obj) {
          list.push(obj[i]);
        }

        this.$set(this.addDeviceLevelForm, 'deviceTypeList', list)
      })
    },
    addDeviceLevelToModal (device) {
      this.addDeviceLevelVisible = true;
      this.$nextTick(() => {
        if (device) {
          this.addDeviceLevelForm = Object.assign({}, device);
          this.addDeviceLevelForm.__type = 'modify'
        } else {
          this.addDeviceLevelForm = {
            levels: []
          };
          this.addDeviceLevelForm.__type = 'add'
        }
      })
    },
    addDeviceLevelSubmit () {
      
      let query = {
        serverCode: this.addDeviceLevelForm.serverCode,
        enterpriseCode: this.addDeviceLevelForm.enterpriseCode,
        levels: this.addDeviceLevelForm.levels,
        deviceType: this.addDeviceLevelForm.deviceType,
        deviceModel: this.addDeviceLevelForm.deviceModel,
        id: this.addDeviceLevelForm.id,
        operator: this.curUser
      }
      let ent = this.enterPriseList.find(v => v.enterpriseCode === query.enterpriseCode)
      let service = ent.serverCodes.find(v => v.code === query.serverCode)

      query.enterpriseName = ent.title;
      query.serverName = service.title;

      let method = this.addDeviceLevelForm.__type === 'add' ? 'deviceTypeLevelControllerAdd' : 'deviceTypeLevelControllerUpdate';

      this.$api[method](query).then(res => {
        console.log(res);
        this.addDeviceLevelVisible = false;
        this.getDeviceLevelList();
      })
    },
    deleteDeviceToModal (device) {
      let list;
      if (!device) {
        list = this.$refs.deviceLevelList.selection;
        if (list.length < 1) {
          this.$message({
            type: 'warning',
            message: '请至少选择一个设备'
          })
          return;
        }
      }
      this.deleteDeviceList = list || [device];
      this.$confirm(`您一共选择了${this.deleteDeviceList.length}个设备`, '是否删除', { type: 'warning' }).then(res => {
        this.$api.deviceTypeLevelControllerDelete({id: this.deleteDeviceList.map(v => v.id)[0]})
      }).catch(() => {

      })
    },
    addAlarmLevelToModal (device) {
      this.addAlarmLevelVisible = true;
      this.$nextTick(() => {
        if (device) {          
          this.addAlarmLevelForm = Object.assign({}, device);
          this.addAlarmLevelForm.alarmLength = device.levels.length - 1
          this.addAlarmLevelForm.__type = 'modify'
        } else {
          this.addAlarmLevelForm = {
            levels: [],
            levelNames: [],
            colors: [],
          };
          this.addAlarmLevelForm.__type = 'add'
        }
      })
    },
    addAlarmLevelSubmit () {
      let query = {
        name: this.addAlarmLevelForm.name,
        serverCode: this.addAlarmLevelForm.serverCode,
        enterpriseCode: this.addAlarmLevelForm.enterpriseCode,
        levels: [],
        levelNames: this.addAlarmLevelForm.levelNames,
        colors: this.addAlarmLevelForm.colors,
        id: this.addAlarmLevelForm.id,
        code: this.addAlarmLevelForm.code,
        state: this.addAlarmLevelForm.state,
      }
      let ent = this.enterPriseList.find(v => v.enterpriseCode === query.enterpriseCode)
      let service = ent.serverCodes.find(v => v.code === query.serverCode)

      query.enterpriseName = ent.title;
      query.serverName = service.title;

      query.operator = this.curUser

      for (let i = 0; i < this.addAlarmLevelForm.alarmLength + 1; i++) {
        query.levels.push(i + 1)
      }

      let method = this.addAlarmLevelForm.__type === 'add' ? 'enterpriseLevelControllerAdd' : 'enterpriseLevelControllerUpdate';
      this.$api[method](query).then(res => {
        this.getAlarmLevelList();
        this.addAlarmLevelVisible = false;
      })
    },
    changeAlarmLevelState (alarmLevel) {
      this.$api.enterpriseLevelControllerUpdateState({
        id: alarmLevel.id,
        code: alarmLevel.code,
        enterpriseCode: alarmLevel.enterpriseCode,
        serverCode: alarmLevel.serverCode,
        state: alarmLevel.state === '启用' ? '禁用' : '启用'
      }).then(res => {
        this.getAlarmLevelList();
      })
    },
    resetAllAlarmSettings (value) {
      this.$set(this.addAlarmLevelForm, 'colors', new Array(value + 1).fill('').map((v, i) => this.defaultAlarmColor[i]))
    },
    deleteAlarmLevelToModal (alarmLevel) {
      this.$confirm(`确定要删除该服务告警等级？<br/>注意：将要删除的服务告警等级是“XXXXX”， 确定删除吗？（包含该告警等级所有数据）`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        this.$api.enterpriseLevelControllerDelete({id: alarmLevel.id}).then(res => {
          this.getAlarmLevelList();
        })
      }).catch(() => {

      })      
    },
    setRelationToModal (device) {
      this.setRelationVisible = true;
      this.$nextTick(() => {
        this.setRelationForm = Object.assign({}, device);
        this.setRelationForm.targetLevelList = device.targetLevelList.map(v => v);
        this.setRelationForm.targetLevelNameList = device.targetLevelNameList.map(v => v);
        this.$api.enterpriseLevelControllerGetLastUse({
          enterpriseCode: device.enterpriseCode,
          serverCode: device.serverCode,
        }).then(res => {
          this.$set(this.setRelationForm, 'targetLevelOptions', res);
        })
      })
    },
    setRelationSubmit () {
      let query = this.setRelationForm;
      query.targetLevelNameList = this.setRelationForm.targetLevelList.map(v => this.setRelationForm.targetLevelOptions.find(u => u.level === v).levelName);
      query.colorList = this.setRelationForm.targetLevelList.map(v => this.setRelationForm.targetLevelOptions.find(u => u.level === v).color);

      this.$api.alarmLevelControllerUpdate(query).then(res => {
        this.getRelationList()
        this.setRelationVisible = false
      })
    },
  },
  mounted () {
    this.getAlarmLevelList();
    this.getDeviceLevelList();
    this.getRelationList();
    this.getUniqueServiceList();
  }
}
</script>

<style lang="less" scoped>
#alarm-level-page{
  background-color: #011838;
}
.left-tree{
  width: 210px;
  margin: 10px;
  margin-right: 0;
  padding: 10px;
  height: calc(100% - 40px);
  background-color: #0B1D3B;
}
.tabs{
  flex: 1;
  height: calc(100% - 20px);
  margin: 10px;
  background-color: #0B1D3B;
}
.list{
  flex: 1;
  overflow: hidden;
  padding-bottom: 10px;
  .table-box{
    flex: 1;
    overflow: hidden;
  }    
}
.alarm-color{
  width: 56px;
  height: 20px;
}
.add-alarm-level-form{
  max-height: 550px; 
  overflow-y: auto;
}
.level-select{
  width: 14px;
  height: 14px;
  position: relative;
  display: block;
  border-radius: 2px;
  border: solid 1px #108EE9;
}
.level-select.selected{
  background-color: #108EE9;
  &::after{
    
    position: absolute;
    top: 1px;
    left: 4px;
    width: 4px;
    height: 8px;
    content: "";
    transition: -webkit-transform .2s ease-in-out;
    -webkit-transition: -webkit-transform .2s ease-in-out;
    transition: transform .2s ease-in-out;
    transition: transform .2s ease-in-out,-webkit-transform .2s ease-in-out;
    -webkit-transform: rotate(0deg) scale(0);
    transform: rotate(0deg) scale(0);
    border-right: 2px solid #fff;
    border-bottom: 2px solid #fff;
    -webkit-transform: rotate(45deg) scale(1);
    transform: rotate(45deg) scale(1);
  }
}
</style>


